
.albums {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow: hidden;
}
.albums .album {
  overflow: hidden;
  position: relative;
  margin: 20px;
}
.albums .album .cover {
  display: block;
  -o-object-fit: cover;
     object-fit: cover;
}
.albums .album .title {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 14px;
  text-shadow: 0 0 2px rgba(255, 255, 255, 0.8);
}



